
import { defineComponent,computed } from "vue";

export default defineComponent({
  name:'jie-header',
  props: {
    //
    title: {
      type: String,
    },
    //
    showMore: {
      type: Boolean,
      default: false,
    },
    //
    showBack: {
      type: Boolean,
      default: false,
    },
  },
  setup(props:any,{emit}) {
    const curTitle =computed(()=>{return props.title});
    //
    const onLookMore = () => {
      emit("onLookMore");
    };
    //
    const onBack = () => {
      emit("onBack");
    };
    return { onLookMore, onBack,curTitle };
  },
});
