//  rem 
function setRem () {
    //  PC
    console.log('')
    // 
    const baseSize = 1920;
    const basePc = baseSize / 1920; // 1920,1920PX
    let vW = document.documentElement.clientWidth;//window.innerWidth; // 
    const vH = document.documentElement.clientHeight;//window.innerHeight; // 
    // 
    const dHeight=937;//1080
    const dueH = vW * dHeight / 1920;
    console.log('',vW,vH,dueH)
    if (vH < dueH) { // 
      vW = vH * 1920 /dHeight
    }
    const rem = vW * basePc; // ,font-size
    document.documentElement.style.fontSize =  rem + "px";
}
// 
setRem();
//  rem
window.onresize = function () {
  setRem()
};