
import { defineComponent, onMounted, watch, toRef } from "vue"; //"@vue/composition-api";
import { guid } from "@/utils/common";
import { EleResize } from "@/utils/esresize.js";
import { init } from "echarts";

export default defineComponent({
  name: "jie-echarts",
  props: {
    //id
    id: {
      type: String,
      default: guid(),
    },
    //
    width: {
      type: String,
      default: "100%",
    },
    //
    height: {
      type: String,
      default: "220px",
    },
    //class
    class: {
      type: String,
      default: "echarts-line",
    },
    //echarts
    option: {
      type: Object,
      default: () => {},
    },
    //style
    style: {
      type: Object,
      default: () => {},
    },
    //
    loading: {
      type: Boolean,
      default: false,
    },
  },
  setup(props: any, { emit }) {
    let MyEcharts: any = null; // echarts
    // 
    const InitCharts = () => {
      let dom = document.getElementById(props.id);
      MyEcharts = init(dom as HTMLDivElement);
      if (props.loading == true) {
        showLoading();
      }
      /**
       * 
       * echartsthis.option
       */
      MyEcharts.clear(); // ,
      MyEcharts.setOption(props.option, true); // true
      MyEcharts.on("click", function (params: any) {
        mapClick(params);
      });
      //
      setTimeout(() => {
        MyEcharts.resize();
      });
      // // -
      // window.onresize = () => {
      //  MyEcharts.resize();
      //     console.log('window');
      //   // MyEcharts.resize({ height: this.height });
      // };
      //divonresize 
      EleResize.on(dom, () => {
        // eslint-disable-line
        console.log("EleResize");
        MyEcharts.resize();
      });
    };
    //
    const mapClick = (params: any) => {
      emit("eclick", params);
    };
    const showLoading = () => {
      if (MyEcharts) {
        MyEcharts.showLoading({
          text: "loading",
          // color: '#4cbbff',
          // textColor: '#4cbbff',
        });
      }
    };
    const hideLoading = () => {
      if (MyEcharts) {
        MyEcharts.hideLoading();
      }
    };
    onMounted(() => {
      InitCharts();
    });
    watch(
      ()=>props.option,
      (newVal, oldVal) => {
        if (MyEcharts) {
          if (newVal) {
            MyEcharts.setOption(newVal, true);
          } else {
            MyEcharts.setOption(oldVal, true);
          }
        } else {
          InitCharts();
        }
      },
      {
        deep: true, //,false
      }
    );
    watch(()=>props.loading, () => {
      props.loading === true ? showLoading() : hideLoading();
    });
    const myclass = toRef(props, "class");
    const myStyle = toRef(props, "style");
    return { MyEcharts, myclass, myStyle };
  },
});
